/* --- Stile generale --- */
.giocatori-page {
  font-family: 'Segoe UI', sans-serif;
  padding: 40px;
  background-color: #f4f4f4;
  color: #1e1e1e;
  min-height: 100vh;
}

/* --- Header --- */
.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}

h2 {
  font-size: 28px;
  color: #007bff;
}

.home-btn {
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border-radius: 6px;
  text-decoration: none;
  font-size: 0.95rem;
  transition: background-color 0.3s ease;
}

.home-btn:hover {
  background-color: #0056b3;
}

/* --- Container principale --- */
.giocatori-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

/* --- Form di inserimento --- */
.form-container {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.06);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

.form-container input {
  padding: 10px;
  width: 220px;
  border-radius: 6px;
  border: 1px solid #ccc;
  font-size: 0.95rem;
}

/* --- Bottone aggiunta --- */
.add-button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 18px;
  border: none;
  border-radius: 6px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-button:hover {
  background-color: #45a049;
}

/* --- Lista dei giocatori --- */
.lista-giocatori {
  width: 100%;
  max-width: 700px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.giocatore-row {
  background-color: white;
  padding: 12px 16px;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  display: flex;
  align-items: center;
  gap: 12px;
}

/* --- Icona giocatore --- */
.player-icon {
  width: 24px;
  height: 24px;
}

/* --- Bottone elimina --- */
.delete-button {
  margin-left: auto;
  background: none;
  border: none;
  color: red;
  font-size: 1.2rem;
  cursor: pointer;
  transition: transform 0.2s ease;
}

.delete-button:hover {
  transform: scale(1.1);
}
.player-icon {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.field-label {
  font-size: 12px;
  color: #555;
  margin-bottom: 12px;
  display: block;
}

.player-icon {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}
